.like_head{
	height: 30px;
	line-height: 40px;
	padding-left: 15px;
	color: #999;
	font-size: 14px;
}
.like_shop{
	display: -ms-flexbox;
	display: flex;
	margin-left: 15px;
	padding:11px 10px 11px 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.shop_content{
	-ms-flex: 1 1;
	    flex: 1 1;
	margin-left: 10px;
}
.shop_content .dealTitle{
	padding:9px 0 13px;
	color: #666;
	font-size: 13px;

	-o-text-overflow: ellipsis;

	   text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}
.shop_content .price{
	height: 22px;
	line-height: 22px;
	padding-right: 4px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}
.shop_content .price .dealgroupPrice{
	font-size: 21px;
	color: #f63;
	font-weight: 700;
}
.shop_content .price .dealgroupPrice i,.shop_content .price .marketPrice i{
	font-style: normal;
	font-size: 14px;
	margin-right: 4px;
}
.shop_content .price .marketPrice{
	font-size: 13px;
	color: #777;
}
.shop_content .price .marketPrice i{
	margin-left: 4px;
}
.shop_content .price .salesdesc{
	font-size: 13px;
	color: #777;
	line-height: 24px;
	
	-o-text-overflow: ellipsis;
	
	   text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}
.like_shop figure{
	position: relative;
}
.like_shop figure span{
	position: absolute;
	left: 0;
	top:0;
	padding:3px 7px;
	background-color: #000;
	font-size: 12px;
	font-weight: 700;
	-webkit-transform: scale(.98);
	    -ms-transform: scale(.98);
	        transform: scale(.98);
	color: #e5bf86;
}
.like_shop figure img{
	width:90px;
	height: 90px;
	display: block;
}
.ad{
	height: 128px;
	margin-left: 15px;
	padding: 10px 15px 10px 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.adItem{
	display: -ms-flexbox;
	display: flex;
	height: 108px;
	-ms-flex-direction: column;
	    flex-direction: column;
}
.ad_content{
	margin-left: 26%;
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	position: relative;
	z-index: 300;
}
.ad_content>p{
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}
.ad_content .title{
	width: 180px;
	font-weight: 700;
	font-size: 18px;
	color: #111;
}
.ad_content .subTitle{
	width: 168px;
	font-size: 12px;
	color: #777;
	margin:8px 0;
}
.ad_content .btn{
	width: 52px;
	text-align: center;
	padding: 8px 14px;
	font-size: 13px;
	line-height: 13px;
}
.ad_container {
	height: 108px;
	margin-top:-108px;
}
.ad_container img{
	width:100%;
	height: 100%;
}


