.hScroll{
	margin-top:10px;
	height: 65px;
	width:100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	overflow: hidden;
}
.headerTitle{
	width:50px;
	height: 65px;
	background: url(../../img/sprite2.png) -86px 18px no-repeat;
	background-size: 186px;
	position: relative;

}
.headerTitle:after{
	position: absolute;
	content: '';
	height: 30px;
	right:3px;
	width: 1px;
	bottom: 15px;
	background: #666;
	-webkit-transform: scaleX(.5);
	    -ms-transform: scaleX(.5);
	        transform: scaleX(.5);
}
.hScroll_inner{
	-ms-flex: 1 1;
	    flex: 1 1;
	height: 65px;
}
.s_Item{
	overflow: hidden;
}
.s_Item a{
	padding:14px 0 13px 10px;
	display: -ms-flexbox;
	display: flex;
	height: 65px;
	width: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.s_title{
	-ms-flex: 1 1;
	    flex: 1 1;
	margin-right: 10px;
	font-size: 14px;
	font-weight: bold;
	color: #111;
	height: 100%;
	float: right;
}
.s_pic{
	height: 100%;
	line-height: 65px;
	display: block;
	margin-right: 10px;
	float: right;
	position: relative;
}
.s_Item img{
	position: relative;
	top: -4px;
	width: 45px;
	height: 45px;
}
.p_count {
	position: absolute;
	bottom: -4px;
	padding:2px 10px;
	border-radius: 10px;
	height: 20px;
	line-height: 20px;
	left: 50%;
	margin-left: -18px;
	z-index: 10;
	background: rgba(0,0,0,.6);
	color: #fff;
	-webkit-transform: scale(.7);
	    -ms-transform: scale(.7);
	        transform: scale(.7);
	font-weight: 500;
}
.move0{
	-webkit-transform: translate3d(0,0,0);
	        transform: translate3d(0,0,0);
	-webkit-transition: none;
	-o-transition: none;
	transition: none;
}
.move1{
	-webkit-transform: translate3d(0,-65px,0);
	        transform: translate3d(0,-65px,0);
	-webkit-transition: all 1s;
	-o-transition: all 1s;
	transition: all 1s;
}
.move2{
	-webkit-transform: translate3d(0,-130px,0);
	        transform: translate3d(0,-130px,0);
	-webkit-transition: all 1s;
	-o-transition: all 1s;
	transition: all 1s;
}
.move3{
	-webkit-transform: translate3d(0,-195px,0);
	        transform: translate3d(0,-195px,0);
	-webkit-transition: all 1s;
	-o-transition: all 1s;
	transition: all 1s;
}
